<template>
  <div class="p2">
    <h1 class="uk-text-center uk-margin-large-top uk-margin-large-bottom">居民企业参股国外企业信息报告表</h1>
    <table cellpadding="0" cellspacing="0" border="1" style="text-align:left; margin: 0 auto">
      <tbody>
      <tr height="30">
        <td colspan="4" style="font-weight:bold;">报告人信息</td>
      </tr>
      <tr height="30">
        <td>企业名称:</td>
        <td>某某机械有限公司</td>
        <td>纳税识别号:</td>
        <td>91370305755666003U</td>
      </tr>
      </tbody>
    </table>
    <table cellpadding="0" cellspacing="0" border="1" style="text-align:left; margin: 50px auto">
      <tbody>
      <tr height="30">
        <td colspan="13" style="font-weight:bold; text-align:left;">被投资外国企业信息</td>
      </tr>
      <tr height="30">
        <td>选择</td>
        <td>外国企业名称</td>
        <td>所在国纳税识别号</td>
        <td>成立地</td>
        <td>主营业务类型</td>
        <td>报告人持股比例</td>
        <td>报告人签字和盖章</td>
        <td>报告日期</td>
        <td>经办人</td>
        <td>联系电话</td>
        <td>所属期起</td>
        <td>所属期止</td>
        <td>操作</td>
      </tr>
      </tbody>
    </table>
    <div style="margin-top: 15px;" class="uk-text-center">
      <button @click="useless" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 新增</button>
      <button @click="useless" class="uk-button uk-button-success"><i class="uk-icon-save"></i> 修改</button>
      <button @click="useless" class="uk-button uk-button-danger"><i class="uk-icon-close"></i> 删除</button>
    </div>
    <div style="width: 1000px; margin:0 auto; margin-top: 50px">
      <Alert type="info" show-icon>
        温馨提示:
        <span slot="desc">
            请先维护被投资外国企业列表，再逐户填报
        </span>
      </Alert>
    </div>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    methods: {}
  }
</script>

<style scoped>
  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    min-width: 1000px;
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }

  input {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    padding: 3px 2px;
    text-align: left;
  }

  .bg-write {
    background: #ccccff;
  }

  .bg-unwrite {
    background: #d6ebff;
  }

  .bg-gray {
    background: #ddd;
  }
</style>



